import {ajax,showTips} from '@/ajax.js'
export default {
	data() {
		return {
			isStatus: 1,
			userphone:'',
			userpassword:'',
			twouserpassword:''
		}
	},
	mounted(){
		
	
	},
	methods:{
		goBtn(){
			let isStatus = 1
			if(this.isStatus==1){
				isStatus = 0;
			}
			this.isStatus = isStatus;
			this.userphone =''
			this.userpassword=''
			this.twouserpassword=''
		},
		setStorageSyncLogin(data,time){
			let timeData = (new Date()).getTime() + 1000 * 60 * 60 * 2;
			uni.setStorageSync('login', data);
			uni.setStorageSync('time', timeData);
		},
		primaryBtn(){
			if(this.userphone==''||this.userphone.length<4){
				showTips('用户名不正确')
				return false;
			}else if(this.userpassword==''||this.userpassword.length<4){
				showTips('密码不正确')
				return false;
			}
			if(this.isStatus==1){ //登录
				uni.request({
					url: ajax.login, 
					data:{
						username:this.userphone,
						userpwd:this.userpassword
					},
					method:'POST',
					success: (res) => {
						if(res.data.code==200){
							this.setStorageSyncLogin(res.data.data.token)
							uni.navigateTo({
								url: '/pages/index/index'
							})
							showTips('登录成功')
						}else{
							showTips('登录失败')
						}
					}
				});
			}else{ //注册 
				if(this.twouserpassword==''||this.twouserpassword.length<4||this.userpassword!=this.twouserpassword){
					showTips('两次密码不一致')
					return false;
				}
				uni.request({
					url: ajax.signup, 
					data:{
						username:this.userphone,
						userpwd:this.userpassword
					},
					method:'POST',
					success: (res) => {
						console.log(res.data.data)
						if(res.data.code==200){
							uni.navigateTo({
								url: '/pages/index/index'
							})
							this.setStorageSyncLogin(res.data.data.token)
							showTips('注册成功')
						}else{
							showTips('注册失败')
						}
					}
				});
			}
		}
	}
}
